.categories {
    &__list {
        display: flex;
        align-items: center;

        @media (max-width: 990px) {
            margin-bottom: 15px;
        }

        @media (max-width: 700px) {
            max-width: 550px;
            overflow-x: scroll;
        }

        @media (max-width: 575px) {
            max-width: 400px;
        }

        @media (max-width: 375px) {
            max-width: 350px;
        }
    }

    &__item {
        margin-right: 15px;
        line-height: 22px;
        font-size: 16px;

        border-radius: 30px;
        background-color: #F9F9F9;
        padding: 10px 18px 10px 18px;
        cursor: pointer;

        &:hover {
            color: #ffffff;
            background-color: #00473C;

            transition: all .3s ease-in-out;
        }

        &--action {
            color: #ffffff;
            background-color: #00473C;
        }
    }

    &__item:last-child {
        margin-right: 0;
    }
}