.popup {
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        background-color: rgba(37,35,35,.6);
        display: flex;
        align-items: center;
        justify-content: center;
        transition: .5s;

        &__close svg {
        width: 40px;
        height: 40px;
        position: absolute;
        right: -53px;
        top: -5px;
        border: none;
        background: none;
        cursor: pointer;

        @media (max-width: 990px) {
            display: none;
         }
    }

    &__close-mobile img {
        display: none;
        width: 40px;
        height: 40px;
        position: absolute;
        left: 55px;
        top: 20px;
        border: none;
        background: none;
        cursor: pointer;

        @media (max-width: 990px) {
            display: block;
         }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        @media (max-width: 990px) {
            flex-direction: column;
            align-items: center;
         }
    }

    &__title {
        font-size: 22px;
        font-weight: 400;
        line-height: 1.15;
        margin-bottom: 8px;

        color: #0e150e;

        @media (max-width: 990px) {
            align-self: flex-end;
         }
    }

    &__ingredients {
        color: #879687;
        font-size: 1.2rem;
        font-family: ProximaSoft,sans-serif;
        line-height: 1.3;
        margin-bottom: 30px;
    }

    &__radius {
        min-width: 350px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;

        border: 2px solid #F0F2ED;
        border-radius: 10px;

        @media (max-width: 990px) {
            min-width: 80%;
         }
    }

    &__select {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__item {
        min-width: 95px;
        padding: 16px 25px;
        cursor: pointer;
    }

    &__item--active {
        background-color: #E6FF55;
        border-radius: 10px;
    }

    &__standart {
        min-width: 350px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;

        border: 2px solid #F0F2ED;
        border-radius: 10px;

        @media (max-width: 990px) {
            min-width: 80%;
         }
    }

    &__standart--active {
        background-color: #E6FF55;
        border-radius: 10px;
    }

    &__bottom {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        padding: 12px 26px 0 12px;
    }

    .standart {
        color: #879687;
        margin-bottom: 25px;
    }
}

.wrapper {
        background: #fff;
        box-shadow: 0px 8px 14px 12px rgba(56, 56, 56, 0.05);
        border-radius: 20px;
        max-width: 830px;
        padding: 35px 20px 12px 43px;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        @media (max-width: 990px) {
            min-width: 100vw;
            height: 100vh;

            border-radius: 0;
         }
}

.standart-item {
    &__item {
        cursor: pointer;
        max-width: 155px;
        padding: 16px 25px;
        margin-right: 30px;

        &:last-child {
            margin-right: 0;
        }
    }
}


.bottom-popup {
    border-top: 1px solid #F0F2ED;

    @media (max-width: 990px) {
        justify-content: space-between;
     }

    &__сount {
        max-width: 100px;
        padding: 16px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-radius: 14px;
        margin-right: 11px;
        background-color: #F0F2ED;
    }

    &__btn {
        max-width: 200px;
        padding: 15px;
        cursor: pointer;
        border-radius: 14px;
        
        color: #ffffff;
        background-color: #00473C;
    }
}

.plus {
    margin-left: 15px;
}

.minus {
    margin-right: 15px;
}



