.pizza-block {
    padding: 30px 0 30px 0;

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;

    }

    &__categories {
        &__list {
            display: flex;
            align-items: center;
        }
    
        &__item {
            margin-right: 10px;
            line-height: 22px;
            font-size: 16px;
        }
    
        &__item:last-child {
            margin-right: 0;
        }
    
        .item-sort__btn {
            border-radius: 30px;
            background-color: #F9F9F9;
            padding: 10px 20px 10px 20px;
            cursor: pointer;
    
            &:hover {
                color: #ffffff;
                background-color: #00473C;
    
                transition: all .3s ease-in-out;
            }
    
            &--action {
                color: #ffffff;
                background-color: #00473C;
            }
        }
    }

    &__title {
        font-weight: 400;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(1, 1fr);
        grid-row-gap: 50px;

    @media (max-width: 1320px) {
        grid-template-columns: repeat(3, 1fr);

        .product {
            max-width: 400px;
        }
    }

    @media (max-width: 990px) {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 150px;

        .product {
            max-width: 400px;
        }
    }

    @media (max-width: 880px) {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 50px;
    }

    @media (max-width: 575px) {
        display: flex;
        flex-direction: column;
    }

        &__item {
            cursor: pointer;
        }
    }
}

.product {
    max-width: 333px;
    padding: 0 18px;

    @media (max-width: 575px) {
        min-width: 100%;
        display: flex;
        align-items: center;
        column-gap: 10px;

        &__img {
            max-width: 200px;
            align-self: flex-start;
        }

        &__price {
            display: none;
        }
    }

    &__img {
        width: 100%;
        margin-bottom: 5px;

        &:hover {
            transform: translateY(3px);
            transition: transform .1s linear;
        }
    }

    &__title {
        font-size: 18px;
        font-weight: 400;
        margin-bottom: 8px;

        @media (max-width: 575px) {
            font-size: 1.6rem;
            line-height: 25px;
        }

        @media (max-width: 375px) {
            margin-bottom: 20px;
        }
    }

    &__descr {
        color: #879687;
        font-size: 15;
        line-height: 20px;
        margin-bottom: 26px;

        @media (max-width: 575px) {
            font-size: 1.6rem;
            line-height: 25px;
        }

        @media (max-width: 375px) {
            display: none;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__price {
        color: #0E150E;
        font-weight: 500;

        font-size: 17px;
        line-height: 24px;
    }

    &__add {
        display: flex;
        justify-content: center;
        align-items: center;

        width: 100px;
        padding: 18px 24px;
        color: #ffffff;
        text-align: center;
        border-radius: 16px;
        cursor: pointer;

        color: #0E150E;
        background-color: #E4E7DE;

        &:hover {
            color: #ffffff;
            background-color: #00473C;

            transition: all .3s ease-in-out;
        }
    }
}