.header {
    padding: 18px 0;

    &__logo img {
        @media (max-width: 375px) {
            max-width: 150px;
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 16px;
    }

    &__button {
        max-width: 140px;
        padding: 18px 60px 18px 60px;
        cursor: pointer;

        border-radius: 14px;
        background-color: #00473C;

        @media (max-width: 375px) {
            padding: 20px 30px;
        }
    }

    &__button-hidden {
        display: none;
    }

    &__button--price {
        display: flex;
        justify-content: center;
        align-items: center;

        max-width: 120px;
        padding: 18px 35px 18px 35px;
        color: #ffffff;

        border-radius: 14px;
        background-color: #00473C;

        @media (max-width: 990px) {
            display: none;
        }
    }

    &__list {
        display: flex;
    }

    &__item {
        line-height: 22px;
        margin-right: 20px;
        color: #0E150E;

        cursor: pointer;
    }

    &__item:hover {
        opacity: .2;
        transition: all .3s ease-out;
    }

    &__item:last-child {
        margin-right: 0;
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

