.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 30px;

    &__list {
        display: flex;
    }

    &__item {
        margin-right: 10px;
        font-size: 48px;
        align-self: center;
    }

    &__btn {
        font-weight: 300;
        padding: 0 15px;
        border-radius: 15px;
        color: #E4E7DE;
        cursor: pointer;

        &:hover {
            background-color: #00473C;
            color: #ffffff;
            transition: all .3s ease-in-out;
        }
    }

    &__btn--arrow {
        color: #E4E7DE;
        cursor: pointer;

        &:hover {
            opacity: .7;
        }

        &:active {
            transform: translateX(10px);
            transition: all .3s ease-in-out;
        }
    }

    &__btn--arrows {
        color: #E4E7DE;
        cursor: pointer;

        &:hover {
            opacity: .7;
        }

        &:active {
            transform: translateX(-5px);
            transition: all .1s ease-in-out;
        }
    }
}