.footer {
    padding: 42px 0 30px 0;
    margin-top: 100px;
    background-color: #F3F4F2;

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;

        margin-bottom: 80px;

        @media (max-width: 990px) {
            display: block;
            margin-bottom: 30px;
        }
    }

    &__number {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 6px;

        color: #0E150E;

        @media (max-width: 990px) {
            margin-top: 20px;
        }
    }

    &__phone-text {
        font-size: 15px;
        line-height: 20px; 

        color: #879687;
    }

    &__list {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        margin-bottom: 50px;

        @media (max-width: 990px) {
            flex-direction: column;
        }
    }

    &__item {
        max-width: 330px;

        @media (max-width: 990px) {
            margin-bottom: 20px;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media (max-width: 990px) {
            display: block;
        }
    }
}

.item-footer {
    &__title {
        font-size: 18px;
        line-height: 25px;
        font-weight: 400;
        margin-bottom: 25px;

        color: #0E150E;
    }

    &__item {
        font-size: 15px;
        line-height: 20px;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__link {
        color: #879687;
    }
}

.bottom-footer {
    &__descr {
        font-size: 15px;
        line-height: 20px;

        color: #0E150E;

        @media (max-width: 990px) {
            margin-bottom: 30px;
        }
    }
    
    &__copyright {
        font-size: 15px;
        line-height: 20px;

        color: #0E150E;
    }

    &__policy {
        font-size: 15px;
        line-height: 20px;

        color: #0E150E;
    }
}

.logo img {
    @media (max-width: 990px) {
        margin-bottom: 30px;
    }
}