input {
    position: relative;
    padding: 10px;
    border-radius: 50px;
    border: 1px solid #333333;

    margin-right: 15px;
}

.search {
    width: 15px;
    height: 15px;
    margin-right: 20px;

    cursor: pointer
}

.search-hidden {
    display: none;
    color: red;
}

.search-close img {
    position: fixed;
    width: 20px;
    height: 20px;
    top: 23px;
    right: 320px;
    cursor: pointer;

    @media (max-width: 375px) {
        right: 35px;
    }
}