@import '../variables';

.cart {
    &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  
    .content__title {
      display: flex;
      align-items: center;
      font-size: 32px;
  
      svg {
        position: relative;
        top: -2px;
        width: 30px;
        height: 30px;
        margin-right: 10px;
        path {
          stroke: #000;
          stroke-width: 1.9;
        }
      }
    }

    &__btn {
      &:hover {
         border-radius: 30px;
         transition: all .2s linear;
      }

      &:active {
        transform: translateY(1px);
      }
    }
  
    &__clear {
      display: flex;
      align-items: center;
      cursor: pointer;
      @include noselect();
  
      span {
        display: inline-block;
        margin-left: 7px;
        color: #b6b6b6;
        font-size: 18px;
      }
  
      span,
      svg,
      path {
        transition: all .2s ease-in-out;
      }
  
      &:hover {
        svg {
          path {
            stroke: darken( #b6b6b6, $amount: 50);
          }
        }
        span {
          color: darken( #b6b6b6, $amount: 50);
        }
      }
    }
  
    &__item {
      display: flex;
      width: 100%;
      border-top: 1px solid #f6f6f6;
      padding-top: 30px;
      margin-top: 30px;
  
      &-img {
        display: flex;
        align-items: center;
        margin-right: 15px;
        width: 10%;
  
        img {
          width: 80px;
          height: 80px;
        }
      }
  
      &-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 40%;
  
        h3 {
          font-weight: bold;
          font-size: 22px;
          line-height: 27px;
          letter-spacing: 0.01em;

          @media (max-width: 665px) {
            font-size: 16px;
            margin-left: 15px;
          }

          @media (max-width: 800px) {
            margin-left: 15px;
          }
        }
  
        p {
          font-size: 18px;
          color: #8d8d8d;

          @media (max-width: 665px) {
            font-size: 14px;
            margin-left: 15px;
          }

          @media (max-width: 800px) {
            margin-left: 15px;
          }
        }
      }
  
      &-count {
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 10px;
        width: 10%;

        @media (max-width: 665px) {
          width: 7%;
        }
  
        &-minus img {
            width: 25px;
            cursor: pointer;

            @media (max-width: 665px) {
              width: 20px;
            }

            &:hover {
              opacity: .5;
            }

            &:active {
              transform: translateY(1.2px);
            }
        }

        &-plus img {
          width: 25px;
          cursor: pointer;

          @media (max-width: 665px) {
            width: 20px;
          }

          &:hover {
            opacity: .5;
          }

          &:active {
            transform: translateY(1.2px);
          }
        }
      }
  
      &-price {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 33%;
  
        b {
          font-weight: bold;
          font-size: 22px;
          letter-spacing: 0.01em;

          @media (max-width: 665px) {
            font-size: 16px;
          }
        }
      }
  
      &-remove {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 4%;
  
        .button {
          border-color: darken($color: #f6f6f6, $amount: 10);
        }
  
        svg {
          transform: rotate(45deg);
  
          path {
            fill: darken($color: $gray-line, $amount: 15);
          }
        }
  
        .button {
          svg {
            width: 11.5px;
            height: 11.5px;
            position: relative;
          }
          &:hover,
          &:active {
            border-color: darken($color: $gray-line, $amount: 80);
            background-color: darken($color: $gray-line, $amount: 80);
          }
        }
      }
    }
  
    &__bottom {
      margin: 50px 0;
  
      &-details {
        display: flex;
        justify-content: space-between;
  
        span {
          font-size: 22px;
  
          &:last-of-type {
            b {
              color: #00473C;
            }
          }
        }
      }
  
      &-buttons {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
  
        .go-back-btn {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 210px;
  
          border-color: darken($color: $gray-line, $amount: 10);
  
          span {
            color: darken($color: $gray-line, $amount: 20);
            font-weight: 500;
            font-weight: 600;
          }
  
          &:hover {
            background-color: #00473C;
            border-color: #00473C;
  
            span {
              color: $gray-line;
            }
          }
  
          svg {
            margin-right: 12px;
            path {
              fill: transparent;
              stroke-width: 2;
            }
          }
        }
  
        .pay-btn {
          font-size: 16px;
          font-weight: 600;
          padding: 16px;
          background-color: #00473C;
          border-radius: 30px;
          cursor: pointer;

          &:hover {
            opacity: .9;
            transition: opacity .3s linear;
          }

          span {
            color: #f6f6f6;
          }
        }
      }
    }
  
    &--empty {
      margin: 0 auto;
      width: 560px;
      text-align: center;
  
      h2 {
        font-size: 32px;
        margin-bottom: 10px;
      }
  
      p {
        font-size: 18px;
        line-height: 145.4%;
        letter-spacing: 0.01em;
        color: #777777;
      }
  
      icon {
        position: relative;
        top: 2px;
      }
  
      img {
        display: block;
        width: 300px;
        margin: 45px auto 60px;
      }
  
      .button--black {
        padding: 12px 0 14px;
        width: 230px;
        margin: 0 auto;
        font-weight: 600;
        font-size: 18px;
      }
    }
  
    .content__items {
      display: block;
    }
  }
  
  .button {
    border-radius: 30px;
    transition: all .2s ease-in-out;

    &:active {
      transform: translateY(2px);
    }

    &--circle {
      cursor: pointer;
      font-size: 16px;
    }

    &--outline {
      width: 30px;
    }
  }
  